const ListButton = (props) => {
  return (
    <div>
      <button className="btn btn-outline-success">
        {" "}
        Boutton 1 {props.name}
      </button>
      <button className="btn btn-outline-success"> Boutton 2 </button>
      <button className="btn btn-outline-warning"> Boutton 3 </button>
    </div>
  );
};
