import "./styles.css";
import Part1 from "./Part1";
import About from "./About";
import Part2 from "./Part2";
import { Component } from "react";

class App extends Component {
  state = {
    page: "Part1"
  };

  pagefinal = () => {
    if (this.state.page === "Part1") {
      return <Part1>jdje</Part1>;
      // block of code to be executed if condition1 is true
    } else if (this.state.page === "Part2") {
      return <Part2>dj</Part2>;
      //  block of code to be executed if the condition1 is false and condition2 is true
    } else {
      return <About>kedke</About>;
      //  block of code to be executed if the condition1 is false and condition2 is false
    }
  };
  Event1 = () => {
    this.setState({
      page: "Part1"
    });
  };
  Event2 = () => {
    this.setState({
      page: "Part2"
    });
  };

  EventAbout = () => {
    this.setState({
      page: "About"
    });
  };

  render() {
    return (
      <div className="text-center">
        <h1 className="titre__render" onClick={this.Event1}>
          {" "}
          Titre{" "}
        </h1>
        <line
          className="btn-group justify-content-between"
          aria-label="Toolbar with button groups"
        >
          <button className="btn btn-dark" onClick={this.Event1}>
            {" "}
            Part 1{" "}
          </button>
          <button className="btn btn-dark" onClick={this.Event2}>
            {" "}
            Part 2{" "}
          </button>
          <button className="btn btn-dark" onClick={this.EventAbout}>
            {" "}
            About{" "}
          </button>
        </line>
        {/*} <p>{this.state.page}</p> */}
        {this.pagefinal()}
      </div>
    );
  }
}

export default App;
