import { Component } from "react";
import ListButton from "./func.js";
import ListPara from "./funcP";
import "./styles.css";
class Part1 extends Component {
  state = {
    name: "Boutton depuis state",
    autre: "Autre state"
  };

  render() {
    return (
      <div>
        <h3 className="titre__part"> Début de la partie 1 </h3>
        <ListButton name={this.state.name}></ListButton>
        <ListPara autre={this.state.autre}></ListPara>
      </div>
    );
  }
}

export default Part1;
